import localization_store from '@/stores/app.store';
import { Component, Host, h, Element, Prop, Watch, State } from '@stencil/core';
import { addDays, format } from 'date-fns';
@Component({
  tag: 'ir-date-popup',
  styleUrl: 'ir-date-popup.css',
  shadow: true,
})
export class IrDatePopup {
  @Prop() dates: { start: Date | null; end: Date | null } = {
    start: null,
    end: null,
  };
  @State() isPopoverOpen = false;
  @Element() el: HTMLIrDatePopupElement;

  private popover: HTMLIrPopoverElement;
  private minDate: Date = addDays(new Date(), -1);

  @Watch('dates')
  handleDatesChange() {
    if (this.dates.end && this.isPopoverOpen) {
      this.popover.toggleVisibility();
    }
  }
  componentWillLoad() {
    this.minDate.setHours(0, 0, 0, 0);
  }
  dateTrigger() {
    return (
      <div class="popover-trigger w-full sm:w-fit" slot="trigger">
        <svg xmlns="http://www.w3.org/2000/svg" height="18" width="12.5" viewBox="0 0 448 512">
          <path
            fill="currentColor"
            d="M96 32V64H48C21.5 64 0 85.5 0 112v48H448V112c0-26.5-21.5-48-48-48H352V32c0-17.7-14.3-32-32-32s-32 14.3-32 32V64H160V32c0-17.7-14.3-32-32-32S96 14.3 96 32zM448 192H0V464c0 26.5 21.5 48 48 48H400c26.5 0 48-21.5 48-48V192z"
          />
        </svg>
        <div class="flex items-center gap-8 flex-1">
          <div>
            <p>Check in</p>
            <p class={'date'}>
              {this.dates.start ? format(this.dates.start, 'MMM dd', { locale: localization_store.selectedLocale }) : <span class="text-slate-500">Add date</span>}
            </p>
          </div>
          <div class="flex flex-col items-end h-full">
            <div>
              <ir-icons name="minus" svgClassName=" h-5 w-5 md:w-3 w-3"></ir-icons>
            </div>
          </div>
          <div>
            <p>Check out</p>
            <p class="date">{this.dates.end ? format(this.dates.end, 'MMM dd', { locale: localization_store.selectedLocale }) : <span class="text-slate-500">Add date</span>}</p>
          </div>
        </div>
      </div>
    );
  }
  render() {
    return (
      <Host>
        <ir-popover ref={el => (this.popover = el)} onOpenChange={e => (this.isPopoverOpen = e.detail)}>
          {this.dateTrigger()}
          <div slot="popover-content" class="date-range-container p-2 md:p-4 border-0 w-full shadow-none sm:border sm:w-auto sm:shadow-sm ">
            <ir-date-range fromDate={this.dates.start} toDate={this.dates.end} locale={localization_store.selectedLocale} maxSpanDays={5} minDate={this.minDate}></ir-date-range>
          </div>
        </ir-popover>
      </Host>
    );
  }
}
